<template>
    <div class = "tab-bar">
        <div class = "container">
            <a href = "javascript:void(0)" @click = "to('/routing')">
                <div>
                    <img v-show = "currentNav !== '/routing'" src = "../../assets/tab-bar/routing.png"/>
                    <img v-show = "currentNav === '/routing'" src = "../../assets/tab-bar/routing-selected.png"/>
                    <span>巡检</span>
                </div>
            </a>
            <a href = "javascript:void(0)" @click = "to('/query2')">
                <div>
                    <img v-show = "currentNav === '/query2'" src = "../../assets/tab-bar/search-selected.png"/>
                    <img v-show = "currentNav !== '/query2'" src = "../../assets/tab-bar/search.png"/>
                    <span>查询</span>
                </div>
            </a>
            <a href = "javascript:void(0)" @click = "to('/setting')">
                <div>
                    <img v-show = "currentNav === '/setting'" src = "../../assets/tab-bar/setting-selected.png"/>
                    <img v-show = "currentNav !== '/setting'" src = "../../assets/tab-bar/setting.png"/>
                    <span>设置</span>
                </div>
            </a>
            <a href = "javascript:void(0)" @click = "to('/userHome')">
                <div>
                    <img v-show = "currentNav === '/userHome'" src = "../../assets/tab-bar/user-selected.png"/>
                    <img v-show = "currentNav !== '/userHome'" src = "../../assets/tab-bar/user.png"/>
                    <span>我的</span>
                </div>
            </a>
        </div>
    </div>
</template>
<script>
export default {
    name:'tab-bat',
    methods:{
        to(targ){
            this.$store.commit('setCurrentNav',targ);
            this.$router.push(targ);
        },
    },
    computed:{
        currentNav(){
             return this.$store.getters.getCurrentNav;
        }
    }
    
}
</script>
<style lang="less" scoped>
    .tab-bar {
        z-index: 999;
        height:inherit;
        background-color:#FAFAFA;
        
        .container{
            height:inherit;
            display: flex;
            justify-content: space-around;
            font-size: 10px;
            div {
                color:#A4AAB3;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                align-content: center;
                width:0.96rem;
                img {
                    width:0.56rem;
                    height: 0.56rem;
                    margin:auto; 
                }
            }
            a {
                color:inherit;
                text-decoration:none;
    
            }
          
        } 
    }
</style>



