<template>
    <div class = "index">
        <div class = "main">
            <router-view></router-view>
        </div>
        <div class = "tab-bar">
            <v-menubar></v-menubar>
        </div>
    </div>
</template>
<script>
import TabBar from '@/components/common/tab-bar'
import Query2 from './Query2'
import Query3 from './Query3'
import Setting from './Setting'
import UserHome from './UserHome'
import Routing from './Routing'

export default {
    name:'index-page',
    components:{
	'v-menubar':TabBar,
    },
    setting:Setting,
    userHome:UserHome,
    query2:Query2,
    query3:Query3,
    routing:Routing
}
</script>

<style lang="less" scoped>
    .index{
        height:100%;
        width:100%;
        .main{
            height:e("calc(100% - 1rem)");
            overflow: auto;
        }
        .tab-bar{
            height:1rem;
            position:fixed;
            bottom:0;
            left: 0;
            right: 0;
        }
    }
</style>



