<template>
    <div class = "title-bar">
        <div class="container">
            <span v-show = "currentNav === '/routing'"> 巡检状态</span>
            <span v-show = "currentNav === '/query2'"> 查询</span>
            <span v-show = "currentNav === '/setting'"> 设置</span>
        </div>
    </div>
</template>
<script>
export default {
    name:'setting-page',
    computed:{
        currentNav(){
             return this.$store.getters.getCurrentNav;
        }
    }
}
</script>

<style lang="less" scoped>
    .title-bar {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 0.8rem;
        background-color: #4AB6A7;
        font-size: 18px;
        padding:0.14rem;
        color:#FFF;
        letter-spacing: 5px;
        position: fixed;
        top:0;
        left: 0;
        right:0;
        z-index: 999;
    }
</style>



