import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/views/Main'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/routing',
      name: 'main.page',
      component: Main,
      children:[
        {
          path:'routing',
          name:'routing.page',
          component:Main.routing
        },
        {
          path:'userSetting',
          name:'userSetting.page',
          component:Main.userSetting
        },
        {
          path:'query2',
          name:'query2.page',
          component:Main.query2
        },
        {
          path:'query3',
          name:'query3.page',
          component:Main.query3
        },
        {
          path:'setting',
          name:'setting.page',
          component:Main.setting
        },
        {
          path:'userHome',
          name:'userHome.page',
          component:Main.userHome
        }
      ]
    }
  ]
})



