<template>
    <div class = "user-setting">
        <div class = "container">
            <div class="top">
                <img src = "../assets/backg/usersetting-top.svg"/>
                <div class = "avatar">
                    <img src = "https://i.loli.net/2017/08/21/599a521472424.jpg" />
                </div>
                <div class = "username">杨小时</div>
            </div>
            <div class="bottom">
                <div>
                    <div class = "setting-options">
                        <div>
                            <span>账号设置</span>
                            <span><Icon type="chevron-right"></Icon></span>
                        </div>
                         <div>
                            <span>报警提交</span>
                            <span>123&nbsp;<Icon type="chevron-right"></Icon></span>
                        </div>
                         <div>
                            <span>提交统计</span>
                            <span><Icon type="chevron-right"></Icon></span>
                        </div>
                    </div>
                    <div class = "exit-button">
                        <mt-button :plain = "true" type = "danger" size = "large">退出</mt-button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'userHome-page'
}
</script>

<style lang="less" scoped>
    .user-setting {
        height: 100%;
        
    }
    .top {
        position: relative;
        height:5rem;
        img {
            width:100%;
            height:5rem;
             //TODO: height for pad
        }
        .avatar {

            overflow: hidden;
            position:absolute;
            top: .82rem;
            left: 0;
            right: 0;
            margin: 0 auto;
            width:2.66rem;
            height:2.66rem;
            border-radius:50%; 
            border:0.1rem solid #B5E1DB;

            img {
                height:inherit;
                width:inherit;

            }
        }
        .username {
            position:absolute;
            top: 4rem;
            left: 0;
            right: 0;
            font-size: 18px;
            color:#FFFFFF;


        }
       
    }
    .bottom {
        padding:0.66rem 0.43rem 0;
        .setting-options {
            div {
                height:0.82rem;
                font-size: 14px;
                color: #4A4A4A;
                background-color: #FFFFFF;
                border:1px solid #C7DDDA;
                border-radius: 3px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 0 0.32rem;
                margin-bottom: 0.24rem;
            }
        }
        .exit-button {
            font-size: 18px!important;
            height:0.8rem;
            margin-top:0.74rem;
            color:#D0021B!important;
        }

    }

</style>



