    <div class="result-items">
        <v-result-item></v-result-item>
        <v-result-item></v-result-item>
        <v-result-item></v-result-item>
 
    </div>
</template>
<style lang="less" scoped>
          .result-items {
            text-align: left;
            margin-top:0.3rem;
            padding: 0 0.32rem;
            padding-right: 0;
            display: flex;
            flex-wrap: wrap;
        }
</style>



