<script>
import TitleBar from '@/components/common/title-bar'
import Echarts from 'echarts'


export default {
    name:'query-page',
    created(){
      
    },
    mounted(){
        this.drawLine();
        this.drawLine2();


    },
    methods:{
        drawLine(){
            // 基于准备好的dom，初始化echarts实例
            let myChart = Echarts.init(document.getElementById('myChart'))
            // 绘制图表
            myChart.setOption({
                title: { text: '温度分布',
                    textStyle:{
                        color:'#4A4A4A ',
                        fontSize:14
                    }
                },
                legend: {  //图例组件
                    //right:100,  //图例组件离右边的距离
                    orient : 'horizontal',  //布局  纵向布局 图例标记居文字的左边 vertical则反之
                    width:40,      //图行例组件的宽度,默认自适应
                    x : 'left',   //图例显示在右边
                    y: 'center',   //图例在垂直方向上面显示居中
                    itemWidth:16,  //图例标记的图形宽度
                    itemHeight:16, //图例标记的图形高度
                    data:['> 1 ℃','> 2 ℃','> 3 ℃','> 4 ℃'],
                    textStyle:{    //图例文字的样式
                        color:'#333',  //文字颜色
                        fontSize:14    //文字大小
                    }
                },
                series: [{
                    name: '销量',
                    type: 'pie',
                    center:['65%','50%'],
                    radius:['30%','80'],
                    itemStyle :{
                        normal : { //normal 是图形在默认状态下的样式；emphasis 是图形在高亮状态下的样式，比如在鼠标悬浮或者图例联动高亮时。
                                label : {  //饼图图形上的文本标签
                                    show : false  //平常不显示
                                },
                                labelLine : {     //标签的视觉引导线样式
                                    show : false  //平常不显示
                                }
                            }
                    },
                    emphasis : {   //normal 是图形在默认状态下的样式；emphasis 是图形在高亮状态下的样式，比如在鼠标悬浮或者图例联动高亮时。
                                label : {  //饼图图形上的文本标签
                                    show : true,
                                    position : 'center',
                                    textStyle : {
                                        fontSize : '10',
                                        fontWeight : 'bold'
                                    }
                                }
                            },
                    data:[
                            {value:80, name:'> 1 ℃'},
                            {value:10, name:'> 2 ℃'},
                            {value:30, name:'> 3 ℃'},
                            {value:20, name:'> 4 ℃'},
                            
                        ]


                }]
            });
        },
         drawLine2(){
            let option = {
                title : {
                    text: '某楼盘销售情况',
                },
             
                legend: {
                    data:['意向','预购','成交'],
                    orient : 'horizontal',  //布局  纵向布局 图例标记居文字的左边 vertical则反之
                    x : 'right',   //图例显示在右边
                    y: 'top',   //图例在垂直方向上面显示居中
                    itemWidth:16,  //图例标记的图形宽度
                    itemHeight:16, //图例标记的图形高

                },
                calculable : false,
                xAxis : [
                    {
                        type : 'category',
                        boundaryGap : false,
                        data : ['周一','周二','周三','周四','周五','周六','周日']
                    }
                ],
                yAxis : [
                    {
                        type : 'value'
                    }
                ],
                series : [
                    {
                        name:'成交',
                        type:'line',
                        smooth:true,
                        itemStyle: {normal: {areaStyle: {type: 'default'}}},
                        data:[10, 12, 21, 54, 260, 830, 710]
                    },
                    {
                        name:'预购',
                        type:'line',
                        smooth:true,
                        itemStyle: {normal: {areaStyle: {type: 'default'}}},
                        data:[30, 182, 434, 791, 390, 30, 10]
                    },
                    {
                        name:'意向',
                        type:'line',
                        smooth:true,
                        itemStyle: {normal: {areaStyle: {type: 'default'}}},
                        data:[1320, 1132, 601, 234, 120, 90, 20]
                    }
                ]
            };


            let myChart = Echarts.init(document.getElementById('myChart2'));
            myChart.setOption(option);

                                



         }
            
    },
    components:{
	'v-titlebar':TitleBar,
    },
}
</script>


<template>
    <div class = "query3">
            <div>
                <v-titlebar></v-titlebar>
            </div>
            <div class="main">
                <div class = "temp-value">
                    <div class = "temp-value-top">
                        <div class = "temp-value-top-left">
                            <img src = "../assets/queryPage/temp-select.png"/>
                            <span style = "font-size:36px;color:#4AB6A7 ">26.8</span>
                        </div>
                        <div class = "temp-value-top-right">
                            <div style = "font-size:12px;color:#4A4A4A ">温差</div>
                            <div style = "font-size:18px;color:#4AB6A7">+12℃</div>
                        </div>
                    </div>
                    <div  class = "temp-value-bottom">
                        <img src = "../assets/queryPage/location.png"/>
                        <span style = "font-size:14px;color:#9B9B9B ">工位12A接线柱</span>
                    </div>
                </div>


                <div class = "temp-range">
                    <div>
                        <div id="myChart" style = "width:6.64rem;height:3.9rem"></div>
                    </div>
                </div>

                <div class = "temp-diff">
                    <div>
                        <div id="myChart2" style = "width:6.64rem;height:3.9rem"></div>
                    </div>
                </div>
            </div>
    </div>
</template>

<style lang="less" scoped>
    .main {
        padding:1.12rem 0.32rem  0.43rem;
        margin-bottom: 1rem;
        height:e("calc(100vh - 4rem)");
        .temp-value, .temp-range,.temp-diff {
            border:1px solid #C7DDDA;
            border-radius: 3px;
            background-color: #FFF;
        }
        >div + div {
            margin-top:0.32rem;
        }
        .temp-value {
            padding:0.32rem 0.32rem 0;
            .temp-value-top,.temp-value-bottom {
                display: flex;
                padding:0.32rem;
            }

            .temp-value-top {
                border-bottom:1px dashed #C7DDDA;
                padding-bottom: 0.32rem;
                > div {
                    flex:1;
                    display: flex;
                    justify-content: flex-start;
                }
                .temp-value-top-left {
                    align-items: center;
                    justify-content: flex-start;
                    img {
                        width:0.64rem;
                        height:0.64rem;
                        margin-right:0.32rem;
                    }
                    
                }
                .temp-value-top-right {
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-end;
                }
            }
            .temp-value-bottom {
                img{
                    width:0.5rem;
                    height:0.5rem;
                }
            }

         }

         .temp-range,.temp-diff {
             padding:.32rem;

         }

         

    
    
    
    
    
    
    
    
    }

</style>



