<script>
import TitleBar from '@/components/common/title-bar'
import Result from '@/components/queryPage/result'

export default {
    name:'query2-page',
    components:{
    'v-titlebar':TitleBar,
    'v-result-items':Result
    },
}
</script>
<template>
    <div>
        <div>
            <v-titlebar></v-titlebar>
        </div>
        <div class="main">
            <div class="search-box">
                <div class = "input-field">
                    <input type="text">
                    <span style = "width:1.36rem;background-color:#4AB6A7;display:flex;align-items:center">
                        <img src = "../assets/queryPage/query.png" style = "width:0.56rem;height:0.56rem;margin:auto" />
                    </span>
                </div>
            </div>
            <div class="result">
                <div class = "result-filter">
                    <span>单相台体-1</span>
                    <span>
                            <Icon type="chevron-down"></Icon>
                    </span>
                </div>
                <v-result-items></v-result-items>
            </div>
        </div>
    </div>
</template>
<style lang = "less" scoped>
    input {
        border:none;
        outline: none;
        font-size: 14px;
    }

    .main {
        padding:0.8rem 0  0.43rem;
        margin-bottom: 1rem;
        height:e("calc(100vh - 4rem)");
    }

    .search-box {
        height: 1.4rem;
        background-color: white;
        padding:0.32rem;
        align-items: center;
        .input-field {
            display: flex;
            border:1px solid #4AB6A7;
            padding-left:0.32rem;
            
            height:0.8rem;
            border-radius: 3px;
            justify-content: space-between;
            input {
                flex:1;
                font-size:14px;
            }
        }
    }
    .result{
        .result-filter {
            margin-top:0.3rem;
            text-align: left;
            padding:0 0.64rem;
            color:#4A4A4A;
            font-size: 14px;
        }
    }

</style>



