export default({
    state:{
        currentNav:'/routing'

    },
    mutations:{
        setCurrentNav(state,targNav){
            state.currentNav = targNav;
            window.sessionStorage.setItem('currentNav',targNav);
          }
    },
    actions:{},
    getters:{
        getCurrentNav:state => {
            if(state.currentNav == ''){
              state.currentNav = window.sessionStorage.getItem('currentNav');
            }
            return state.currentNav;
        }
    },
    modules:{},
    pludins:[],
})


