<template>
    <div class = "setting">
        <div class="container">
            <div>
                <v-titlebar></v-titlebar>
            </div>
            <div class = "settings">

                <div class = "setting-item">
                    <div>温度报警值</div>
                    <div class = "input-group">
                        <div class = "input-item">
                            <span>最大值:</span><div class  = "input-field"><input type="number"> ℃</div>
                        </div>

                        <div class = "input-item">
                            <span>最小值:</span><div  class  = "input-field"><input type="number" > ℃</div>
                        </div>
                    </div>
                </div>

                <div class = "setting-item">
                    <div>温差报警值</div>
                    <div class = "input-group">
                        <div class = "input-item">
                            <span>最大值:</span><div class  = "input-field"><input type="number"> ℃</div>
                        </div>
                    </div>
                </div>

                <div class = "setting-item">
                    <div>报警方式</div>
                    <div class = "input-group" >
                        <RadioGroup size = "large">
                            <Radio > <font style = "font-size:inherit">震动</font></Radio>
                            <Radio ><font style = "font-size:inherit">铃声</font></Radio>
                            <Radio ><font style = "font-size:inherit">震动+铃声</font></Radio>
                        </RadioGroup>
                    </div>
                </div>

                <div class = "setting-item" style = "border:none">
                    <div></div>
                    <div class = "input-group" style = "border:none" >
                        <Button size = "large" type="success" style = "background-color:#4AB6A7" long>保存</Button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import TitleBar from '@/components/common/title-bar'
export default {
    name:'setting-page',
    components:{
	'v-titlebar':TitleBar,
    },
   

}
</script>

<style lang="less" scoped>
    .settings {
       margin:0.32rem 0.43rem;
       border:1px solid #C7DDDA;
       border-radius: 3px;
       background-color: #FFF;
       padding:0 0.32rem 0.2rem ;
       text-align: left;
       font-size: 14px;
       margin-top:1.12rem;
       .setting-item {
           margin-top:0.28rem; 
            input {
                border:none;
                outline: none;
                font-size: 14px;
                max-width: 0.8rem;
            }
            border-bottom:1px dashed #C7DDDA;
            padding-bottom:0.4rem;
            .input-group {
                display: flex;
                margin-top:6px;
                .input-item {
                    width:2.76rem;
                    display: flex;
                    justify-content: space-around;
                    align-items: center;

                    .input-field {
                        display: flex;
                        justify-content: space-around;
                        flex:.5;
                        padding:6px 13px;
                        border:1px solid #C7DDDA;
                        border-radius: 3px;



                    }
                }
            }

       }
    }

</style>



