<script>
import TitleBar from '@/components/common/title-bar'
import Result from '@/components/queryPage/result'

export default {
    name:'routing-page',
    components:{
    'v-titlebar':TitleBar,
    'v-result-items':Result
    },
    data(){
        return{
            sheetVisible:false,
            actions:[
                {name:'古豪',method:()=>{
                    alert("古豪");
                }},
                {name:'张建',method:()=>{
                    alert("张建");

                }},
                {name:'金总',method:()=>{
                    alert("金总");

                }}
                
                
                ]
        }
    },
    methods:{
        select(){
        this.sheetVisible = true;
        }
    }
    
}
</script>
<template>
    <div>
        <div>
            <v-titlebar></v-titlebar>
        </div>
        <div class="main">
            <div class="result">
                <div class = "result-filter">
                    <div style = "display:flex;align-items:center" @click="select()">
                        <img src="../assets/queryPage/location-green.png">
                        <span>全部片区——&nbsp;全部位置(5)</span>&nbsp;&nbsp;
                        <img src="../assets/queryPage/filter-green.png">
                    </div>
                     
                    <i-switch size="large" style = "color:red!important">
                        <span slot="open" style = "font-weight:bold">报警</span>
                        <span slot="close">全部</span>
                    </i-switch>
                </div>
                <v-result-items></v-result-items>
                
                <div class = "submit-button">
                    <Button size = "large" type = "primary" long style = "font-size:18px;">提交</Button>
                </div>
            </div>
        </div>
        <mt-actionsheet
        v-model="sheetVisible"
        :actions="actions">
        </mt-actionsheet>
    </div>
</template>
<style lang = "less" scoped>
    input {
        border:none;
        outline: none;
        font-size: 14px;
    }

    .main {
        padding:0.8rem 0  0.43rem;
        margin-bottom: 1rem;
        height:e("calc(100vh - 4rem)");
        position: relative;
        
    }
    .result{
        .result-filter {
            margin-top:0.3rem;
            text-align: left;
            padding:0 0.64rem;
            color:#4A4A4A;
            font-size: 14px;
            display: flex;
            justify-content: space-between;
            img {
                width:0.32rem;
                height:0.32rem;
            }
        }
        .submit-button {
            padding:0 0.32rem;
            position: fixed;
            bottom: 1.32rem;
            left:0;
            right:0;
        }
    }

</style>



